.menu-dish-maker-page {
  .menu-dish-maker-page-container {
    overflow: auto;
    .selected-box {
      .selected-item {
          border-radius: 10px;
          .MuiOutlinedInput-input {
              border: none;
              height: 45px;
              border-radius: 10px !important;
              // padding: 11px 12px;
              box-sizing: border-box;
              min-width: 110px;
              display: inline-flex;
              align-items: center;
          }
          .MuiOutlinedInput-notchedOutline {
              border: 0;
          }
      }
      .box-shadow-search {
          box-shadow: 0px 3px 5px -3px rgba(0, 0, 0, 0.1), 0px 0px 10px 0px rgba(0, 0, 0, 0.08),
              0px 3px 10px 1px rgba(0, 0, 0, 0.07);
      }
  }
  }
}
.menu-excel-btn {
  margin-right: -8px;
}
