.deleted-resource-page {

    .contain {
        
        // width: 100%;
        .table-top-tool {
            display: flex;
            margin-bottom: 12px;
            width: auto;
            // justify-content: flex-end;
            .select-input {
                // width: 100px;
                // margin-right: 8px;
                // margin-left: 8px;
                .select-input-single {
                    width: 120px;
                }
            }
        }

        .extraTable {

        }

        .featureList {
            display: flex;
            justify-content: flex-end;
    
            div {
                &:nth-of-type(2) {
                    margin: 0 32px;
                }
            }
    
            .iconButton {
                // width: 32px;
                // height: 32px;
                border-radius: 4px;
                min-width: 34px;
                padding: 4px;
            }
    
            .ico {
                color: white;
            }
        }
    }
}

.deleteResource-dialog-content {

    .pic-box {
        height: 140px;
        width: 140px;
        margin-right: 1.5rem;
        border-radius: 6px;
        background-color: #f8f7fa;
        display: inline-block;

        .pic-box-img {
            height: 100%;
            width: 100%;
            border-radius: 6px;
        }
    }
    .right-content {
        display: flex;
        flex: 1;
        flex-direction: column;
        justify-content: space-around;

        .op-card {
            align-items: flex-end;
            justify-content: flex-end;
            width: 100%;
            flex-direction: row;
            display: flex;
        }
    }
}