@import "../../../themes/_themes-vars.module.scss";

.common-menu-list {
  width: 100%;
  max-height: 84vh;
  max-width: 250px;
  overflow: auto;
  // background-color: rgb(255, 255, 255);
  &::-webkit-scrollbar {
    display: none;
  }
  .MuiListItem-root {
    // color:#6e6e6e;
    &.active {
      color: $primaryMain;
      .menu-icon{
        color: $primaryMain;
      }
    }
    &.activeContextMenu {
      box-shadow: 0 0 10px 5px rgba(63, 63, 68, 0.05%),
        0 0 10px 5px rgba(33, 34, 81, 0.05%);
    }
  }
  .sub-menu {
    .MuiList-root {
      // background: rgb(247, 247, 255);
      .MuiListItem-root {
        padding-left: 32px;
      }
    }
  }
  &.fullWidth {
    max-width: unset;
  }
  .menu-icon{
    font-size: 16px
  }
}
