@import "../../../themes/themes-vars.module";
.reserve-meal-period-page {
    .reserve-meal-period-content-box {
        display: flex;
        flex-direction: column;
        margin-left: 24px;
        .selected-week-btn {
            background: $primaryMain;
            color: #ffffff;
        }
        .week-meal-period-box {
            display: flex;
            flex-direction: column;
            margin-bottom: 30px;
            .week-title {
                font-weight: bold;
                margin: 10px 0;
                font-size: 18px;
            }
            .add-period-btn {
                background: $primaryMain;
                color: #ffffff;
                height: 45px;
                margin: 15px 0;
                align-self: flex-start;
                .MuiSvgIcon-root {
                    margin-right: 5px;
                }
            }
            .period-content-box {
                display: flex;
                flex-direction: column;
                height: auto;
                min-height: 40vh;
                overflow-x: unset;
                overflow-y: scroll;
                padding: 5px;
            }
        }
    }

    .reserve-period-card {
        margin-top: 16px;
        padding: 20px 8px 16px;
        box-shadow: 0px -2px 4px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14),
            0px 1px 5px 0px rgba(0, 0, 0, 0.12);
        .card-top-bar {
            display: flex;
            align-items: center;
            margin-bottom: 12px;
            padding: 0 15px 15px 15px;
            border-bottom: 1px solid #efefef90;

            .delete-period-btn {
                background: $primaryMain;
                color: #ffffff;
                min-width: 110px;
                margin-left: 10px;
            }
        }
        .card-content-box {
            display: flex;
            flex-direction: column;
            padding: 0 15px;
            .card-content-title {
                display: flex;
                align-items: center;
                font-weight: bold;
                font-size: 15px;
                margin-bottom: 15px;
                .MuiSvgIcon-root {
                    font-size: 15px;
                    margin-right: 5px;
                }
            }
            .peoriod-times-box {
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                .peoriod-time-tab {
                    display: flex;
                    align-items: center;
                    border-width: 1px;
                    border-style: solid;
                    border-radius: 4px;
                    height: 40px;
                    padding: 0 5px;
                    margin: 3px 10px 3px 0;
                    font-size: 14px;
                    .time-tab-label {
                        line-height: 40px;
                        margin: 0 10px;
                    }
                    .MuiSvgIcon-root {
                        cursor: pointer;
                    }
                }
            }
            .peoriod-times-picker {
                display: flex;
                align-items: center;
                margin-top: 20px;
                .times-picker-center-text {
                    margin: 0 15px;
                    line-height: 50px;
                }
                // .MuiInputBase-root {
                //     background-color: #ffffff;
                //     border-radius: 50px;
                //     height: 50px;
                //     border: 1px solid #e6e8f3;
                //     .MuiInputBase-input {
                //         font-size: 16px;
                //         text-align: center;
                //     }
                //     .MuiIconButton-root {
                //         padding: 8px;
                //     }
                //     &::before {
                //         display: none !important;
                //     }
                //     &::after {
                //         display: none !important;
                //     }
                // }
                .times-add-btn {
                    color: #ffffff;
                    border-radius: 50%;
                    min-width: 0;
                    width: 45px;
                    height: 45px;
                    margin-left: 10px;
                }
            }
        }
    }

    .reserve-meal-people-card {
        margin-bottom: 15px;
        .reserve-basic-input {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            .input-label {
                font-size: 15px;
                font-weight: bold;
            }
            .base-input {
                width: 40%;
            }
            .MuiInputLabel-root {
                font-size: 15px;
            }
            .MuiInputBase-root {
                font-size: 15px;
            }
        }
    }
}
