.account-manager-panel-content {
    display: flex;
    position: relative;
    flex-direction: column;

    .table-content {
        min-width: 100px;
        width: 100%;
        margin-top: 10px;
    }

    .topbar {
        display: flex;
        flex-direction: row;
        align-items: center;
    }
}

.featureList {
    display: flex;
    justify-content: flex-end;

    div {
        &:nth-of-type(2) {
            margin: 0 32px;
        }
    }

    .iconButton {
        width: 32px;
        height: 32px;
        border-radius: 4px;
    }

    .ico {
        color: white;
    }
}

.edit-account-container {
    display: flex;
    flex-direction: column;


    .dialog-content {
        display: flex;
        position: relative;
        flex-direction: column;
    }

    .base-info {
        margin-bottom: 30px;
    }

    .combind-restaurant {
        margin-bottom: 30px;
    }

    .auth-setting {
        margin-bottom: 10px;
    }

    .phone-country {
        display: flex;
    }
}

.search-tool {
    display: flex;
    flex-direction: row;
    margin-bottom: 10px;

    .select-input {
        // width: 100px;
        margin-right: 10px;
    }
}

.account {
    display: flex;
    flex-direction: column;
    position: relative;

    .phone-country {
        display: flex;
        position: absolute;
        z-index: 3;
        top: 25px;
        left: 5px;
    }

    .account-input {
        input {
            padding-left: 50px;
        }
    }
}