.group-account-page {

    margin-top: 5px;
    // background: inherit;
    // background-color: rgba(255, 255, 255, 1);
    // box-sizing: border-box;
    // border: 1px solid rgba(221, 231, 249, 1);
    // border-radius: 4px;
    // -moz-box-shadow: 0px 10px 20px rgba(67, 67, 67, 0.117647058823529);
    // -webkit-box-shadow: 0px 10px 20px rgba(67, 67, 67, 0.117647058823529);
    // box-shadow: 0px 10px 20px rgba(67, 67, 67, 0.117647058823529);
    // margin: 10px 35px 20px;
    // padding-bottom: 15px;
    .skeleton {
        // .firstSkeleton {
        //     display: flex;
        //     flex: 1;
        //     margin: 20px;
        // }

        // .leftSkeleton {
        //     flex: 1;
        //     margin-right: 20px;
        // }

        // .rightSkeleton {
        //     flex: 1;
        // }

        .generalSkeleton {
            border-radius: 4px;
            // margin-bottom: 20px;
        }
    }

    .contain {
        .top-button-group {
            margin-bottom: 10px;
            display: flex;
            justify-content: flex-end;

            // width: 100px;
            .button-group {
                margin-left: 15px;
                width: 40px;
                height: 40px;
                // min-width: 20px;
                // .button-icon {
                //     display: flex;
                //     align-items: center;
                // }
            }
        }

        .pic-box {
            height: 140px;
            width: 140px;
            margin-right: 1.5rem;
            border-radius: 6px;
            background-color: #f8f7fa;
            display: inline-block;

            .pic-box-img {
                height: 100%;
                width: 100%;
                border-radius: 6px;
            }
        }

        .featureList {}

        .iconButton {
            // width: 35px;
            // height: 35px;
            margin-right: 10px;
            // border-radius: 4px;
        }
    }



    .firstContainer {
        display: flex;
        flex: 1;
    }

    .mainAndConcat {
        flex: 1;

        .title {
            font-family: "Krub";
            font-weight: 400;
            font-style: normal;
            font-size: 20px;
            margin: 25px 0 16px;
        }

        .contactUs {
            margin: 0 48px 0 24px;

            & .MuiFormControl-root {
                flex: 1;

                &:nth-of-type(1) {
                    width: 100%;
                }
            }
        }

        .phoneContainer {
            width: 100%;
            display: flex;
            margin-top: 41px;
        }

        .codeContainer {
            position: relative;
            box-sizing: border-box;
            width: 137px;
            height: 56px;
            border-radius: 4px;
            background: #f5f7fc;
            margin-right: 8px;
            padding-left: 10px;

            p {
                font-weight: 400;
                font-style: normal;
                font-size: 13px;
                color: #8897ca;
                padding-top: 5px;
            }

            .extraSelect {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
            }
        }
    }

    .addressAndContact {
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
        flex: 1;
        padding-right: 32px;

        .title {
            font-family: "Krub";
            font-weight: 400;
            font-style: normal;
            font-size: 20px;
            margin: 25px 0 16px;
        }

        .addressContainer {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }

    // & .MuiInputBase-input {
    //     color: #575988;
    // }

    & .Mui-disabled {
        color: rgb(170, 170, 170);
    }
}