// 基本信息组件
.basic-info {
  .base-info-content {
    display: flex;
    align-items: flex-start;
    padding: 24px 0px;
    border: none;
    outline: none;
    background-color: #fff;
    .basic-info-left {
      .basic-info-avatar {
        width: 120px;
        height: 120px;
        // margin-right: 60px;
      }
      .basic-info-avatar-upload {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 120px;
        height: 120px;
        border: 1px solid rgba(145, 158, 171, 0.32);
        margin: auto;
        border-radius: 50%;
        padding: 8px;
        border-image: initial;
        .basic-info-style {
          width: 100%;
          height: 100%;
          display: flex;
          position: relative;
          align-items: center;
          flex-direction: column;
          justify-content: center;
          color: rgb(99, 115, 129);
          background-color: rgb(244, 246, 248);
          transition: opacity 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
          border-radius: 50%;
          .text {
            font-family: "Public Sans", sans-serif;
            font-weight: 400;
            font-size: 0.75rem;
            line-height: 1.5;
            margin: 0px;
            padding-top: 5px;
          }
        }
      }
    }
    .head {
      border-bottom: 1px solid #cecece60;
      margin-bottom: 8px;
    }
    .basic-info-right {
      flex: 1;
      margin-left: 40px;
      .basic-info-input {
        display: flex;
        flex-direction: row;
        // margin-bottom: 10px;
        .type-text {
          min-width: 80px;
        }
        .uid-type-text {
          min-width: 80px;
          color: #8e8e8e;
        }
        .uid-text {
          color: #8e8e8e;
        }
        .name-text {
          font-size: 25px;
          font-weight: 600;
        }
      }
    }
  }
  &.MuiPaper-elevation1 {
    box-shadow: rgba(50, 50, 93, 0.03) 0px 2px 5px -1px,
      rgba(0, 0, 0, 0.05) 0px 1px 3px -1px;
    border-radius: 6px;
  }
  &.MuiPaper-elevation0 {
    box-shadow: none;
  }
}
// 表格样式调整
.MUIDataTable {
  position: relative;
  .table-loading {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffff99;
    transition: opacity 225ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  }
  .table {
    box-shadow: none;
    .MuiPaper-elevation2 {
      box-shadow: none;
    }
  }

  // 工具栏按钮样式调整
  .MuiPaper-root .MuiPaper-root,
  .MuiToolbar-root {
    .MuiIconButton-root {
      // color: #4191ff;
      // background-color: #4191ff26;
      border-radius: 4px;
      padding: 6px 8px;
      min-width: 40px;
      min-height: 40px;
      margin-left: 8px;
      // &:hover {
      //   background-color: #b8e0fe;
      //   color: #4191ff;
      // }
    }
    input {
      height: 40px;
      box-sizing: border-box;
    }
  }
  .MuiPaper-root .MuiPaper-root{
    padding: 16px 0;
  }
}

.rbp-search-box {
    .search-item {
        border-radius: 10px;
        .MuiOutlinedInput-input {
            border: none;
            height: 45px;
            border-radius: 10px !important;
            padding: 11px 12px;
            box-sizing: border-box;
            min-width: 100px;
            display: inline-flex;
            align-items: center;
        }
        .MuiOutlinedInput-notchedOutline {
            border: 0;
        }
    }
    .searchInput {
        height: 46px;
        // color: #000;
        // background: #fafafa;
        border: none;
        outline: none;
        padding: 8px 15px;
        border-radius: 10px;
        margin-left: 20px;
        .searchIconBtn {
            cursor: pointer;
            border-radius: 50%;
            min-width: 40px !important;
        }
    }
    .search-back-box {
        margin: 0 10px;
        border-radius: 6px;
    }
    .timePicker {
        .MuiFormControl-root {
            width: 200px;
            /* height: 0;
            border-radius: 10px;
            color: #000; */
        }
    }
    .box-shadow-search {
        box-shadow: 0px 3px 5px -3px rgba(0, 0, 0, 0.1), 0px 0px 10px 0px rgba(0, 0, 0, 0.08),
            0px 3px 10px 1px rgba(0, 0, 0, 0.07);
    }
}
