.AuthGroup-panel-content {
    display: flex;
    position: relative;

    .sidebar {
        width: 150px;
        align-items: center;
        justify-content: center;
        margin-right: 10px;
        min-height: 200px;
        padding-top: 20px;

        .operation {
            justify-content: center;
            align-items: center;
            text-align: center;
            margin-bottom: 6px;


            .add-button {
                text-align: left;
                font-size: 20px;
                width: 100px;
                margin-right: 5px;
            }

            .delete-button {
                // background-color: #fc6947;
                margin-left: 5px;
            }

            .bigger {
                width: 40px !important;
                height: 40px !important;
            }
        }
    }

    .right-content {
        display: flex;
        flex: 1;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;

        .op-card {
            align-items:  center;
            justify-content: flex-start;
            width: 100%;
            display: flex;
            flex-direction: row;
            padding-left: 10px;
            padding-right: 10px;
            padding-top: 5px;
            padding-bottom: 5px;
            margin-bottom: 5px;
        }
    }
}