.tool-bar-button {
    display: flex;
    color: rgb(158, 158, 158);
    align-items: center;
    .app-bar-avatar {
        cursor: pointer;
        background: rgb(158, 158, 158);
    }
}

.top-bar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    box-sizing: border-box;
    z-index: 102;
    padding: 0rem 1.5rem !important;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    &::after {
        top: 0px;
        left: 0px;
        z-index: -1;
        width: 100%;
        content: "";
        position: absolute;
        backdrop-filter: blur(10px);
        height: calc(64px + 1rem);
        -webkit-mask: linear-gradient(rgb(248, 247, 250), rgb(248, 247, 250) 18%, transparent 100%);
        background: linear-gradient(
            rgba(248, 247, 250, 0.7) 44%,
            rgba(248, 247, 250, 0.43) 73%,
            rgba(248, 247, 250, 0)
        );
        // -webkit-mask: linear-gradient(rgb(37, 41, 60), rgb(37, 41, 60) 18%, transparent 100%);
        // background: linear-gradient(rgba(37, 41, 60, 0.7) 44%, rgba(37, 41, 60, 0.43) 73%, rgba(37, 41, 60, 0));
    }

    .bg {
        // background-color: rgba(255, 255, 255, 0.72);
        // box-shadow: rgba(145, 158, 171, 0.6) 0px 5px 5px -2px, rgba(145, 158, 171, 0.24) 0px 16px 32px -4px;
        // position: sticky;
        // top: 0;
        // backdrop-filter: blur(6px);
        // height: 58px;
        // box-sizing: border-box;
        // z-index: 102;
    }
    .contentBox {
        height: 64px;
        margin-top: 1rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 0rem 1.5rem !important;
    }
    .content {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        .icon {
            font-size: 36px;
            cursor: pointer;
        }
        .text {
            font-size: 18px;
            font-weight: 600;
            margin-left: 20px;
        }
        .line {
            border-left: 2px solid #e8e8e8;
            height: 30px;
            margin-left: 7px;
            margin-right: 7px;
        }
    }
    .top-left {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        overflow: hidden;
        text-overflow: ellipsis;
        word-wrap: break-word;
        white-space: nowrap;
        .text {
            font-size: 20px;
            font-weight: 600;
            min-height: 35px;
            overflow: hidden;
            text-overflow: ellipsis;
            word-wrap: break-word;
            white-space: nowrap;
            width: 100%;
        }
        .type-text {
            font-size: 14px;
            color: #376fd0;
            cursor: pointer;
        }
        .current-text {
            font-size: 14px;
            color: #858282;
            // color: #858282;
        }
    }
}
