.group-unit {
    flex-direction: column;
    align-items: flex-start;

    .panel-content {
        display: flex;
        margin: 10px;
        border-style: solid;
        border-color: #e0dfee;
        padding: 20px;
        border-radius: 10px;
        position: relative;

        .sidebar {
            width: 100px;
            align-items: center;
            border-style: solid;
            border-color: #e0dfee;
            border-radius: 8px;
            padding: 5px;
            margin-right: 10px;
        }
    }

    .featureList {
        display: flex;
        justify-content: flex-end;

        div {
            &:nth-of-type(2) {
                margin: 0 32px;
            }
        }

        .iconButton {
            width: 32px;
            height: 32px;
            border-radius: 4px;
        }

        .ico {
            color: white;
        }
    }

    .return-button {
        margin-right: 4px;
        .return-button-icon {
            display: flex;
            align-items: center;
        }
    }
}
