.group-management-page {

    --width: clamp(200px, 45vmin, 500px);

    .table-top-tool {
        display: flex;
        margin-bottom: 12px;
        justify-content: space-between;

        .left-tool {
            display: flex;
            width: auto;

            .left-tool-delete-button {
                margin-right: 8px;
                // width: 40px;
                // height: 40px;
            }

            .select-input {
                // width: 120px;
                margin-right: 8px;
                margin-left: 8px;

                .select-input-single {
                    width: 120px;
                }
            }
        }

        .right-tool {
            .select-input {
                // width: 100px;
            }
        }
    }



    .extraTable {
        .sticky {
            // position: sticky;
        }

        .collapse-content {
            margin: 25px 10px;

            .accordion-box {
                // 右边渐入动画
                .fade-in-right {
                    opacity: 0;
                    transform: translateX(100%);
                    // animation: fade-in-right 0.4s ease-out forwards;
                    animation: fade-in-right 0.180s ease-out both;
                }
            }

            @keyframes fade-in-right {
                to {
                    opacity: 1;
                    transform: translateX(0%);
                }
            }

            // @keyframes fade-in-right {
            //     0% {
            //         opacity: 0;
            //         transform: translate3d(100%, 0, 0);
            //     }

            //     100% {
            //         opacity: 1;
            //         transform: translate3d(0, 0, 0);
            //     }
            // }
        }

        .group-card {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;

            .card {
                display: flex;
                box-sizing: border-box;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                // background: #4d5bdb;
                // border-radius: 7px;
                color: #fff;
                // height: 30px;
                // width: 30px;
                // padding: 5px 8px;
                margin-right: 6px;
                margin-top: 6px;
                perspective: 10px;
                cursor: pointer;
                transform: translateZ(0);
                transition-duration: 0.1s;
                transition-property: box-shadow, transform;

                // &::before {
                //     position: absolute;
                //     top: 0;
                //     // left: 0;
                //     right: 0;
                //     content: "";
                //     // border-top: 20px solid #ff8058;
                //     // border-right: 20px solid #ff8058;
                //     // border-bottom: 20px solid #ff8058;
                //     // border-left: 20px solid #ff8058;
                //     // width: 0;
                //     // height: 0;
                //     // border-top-right-radius: 10px;
                //     background-color: #ff8058;
                // }

                .card-box-shadow {
                    inset: 0;
                    box-shadow:
                        calc(var(--width) * 0.006) calc(var(--width) * 0.008) calc(var(--width) * 0.02) 0 hsla(210, 10%, 100%, 0.90) inset,
                        calc(var(--width) * -0.01) calc(var(--width) * -0.01) calc(var(--width) * 0.04) 0 hsla(213, 13%, 23%, 0.2) inset;
                }


                // &:hover,
                // :focus,
                // :active {
                //     transform: scale(1.04);
                //     box-shadow: 0 5px 8px -8px rgba(0, 0, 0, 0.5);
                // }

                // .item,
                // .key {
                //     display: flex;
                //     align-items: center;
                //     color: #a2b6ff;
                // }

                // .key {
                //     margin: 2px 0 0 3px;
                // }

                // .icon {
                //     margin-right: 3px;

                //     &:nth-of-type(1) {
                //         font-size: 22px;
                //     }

                //     &:nth-of-type(2) {
                //         font-size: 16px;
                //     }
                // }
            }
        }


    }

    .featureList {
        display: flex;
        justify-content: flex-end;

        div {
            &:nth-of-type(2) {
                margin: 0 32px;
            }
        }

        .iconButton {
            // width: 32px;
            // height: 32px;
            border-radius: 4px;
            min-width: 34px;
            padding: 4px;
        }

        .ico {
            color: white;
        }
    }
}

.group-unit {
    background-color: transparent !important;
}