@import "../../../themes/_themes-vars.module.scss";

.dinner-table-page {
    overflow: hidden;
    -webkit-overflow-scrolling: auto;
    padding: 10px;
    flex-wrap: nowrap;

    .action-box {
        display: flex;
        justify-content: space-between;

        .view-tip-text {
            font-size: 12px;
            color: $primaryMain;
        }
    }

    .plan-container {
        position: relative;
        height: calc(100vh - 180px);
        background: #dadae1;
        border-radius: 8px;

        .floor-plan-bottom-feature {
            position: absolute;
            bottom: 10px;
            left: 10px;
            z-index: 9;
        }
    }
}

.collapse-title {
    display: flex;
    align-items: center;
    .collapse-icon {
        font-size: 18px;
    }
    .collapse-tip {
        font-size: 12px;
        color: #b6b6b6;
        margin-left: 5px;
    }
}

.dinner-table-dialog {
    .MuiDialog-paper {
        min-width: 300px;
    }

    .view-tip-text {
        font-size: 12px;
        color: $primaryMain;
    }

    .mb16 {
        margin-bottom: 16px;
    }


    .up-down-remove-area {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 16px;
        padding: 5px;
    }
}

.table-icon {
    cursor: pointer;
    color: $primaryMain;
}

.disable-icon {
    color: rgba(0, 0, 0, 0.26);
}

.table-upload-component {
    display: inline-block;
    padding-left: 0px;
    margin-right: 8px;

    .table-upload-btn {
        color: #4191ff;
        background-color: #4191ff26;
        padding: 6px 10px;
        padding-bottom: 8px;
        cursor: pointer;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-radius: 4px;
        height: 40px;
        box-sizing: border-box;
        font-size: 0.875rem;


        &:hover {
            background-color: #b8e0fe;
        }
    }
}