@import "../../../../themes/themes-vars.module";

.auth-card-list {
}
.auth-card-button {
    margin: 10px !important;
    border-radius: 10px !important;
    .auth-card-item {
        background-color: $primaryMain !important;
        border-radius: 10px !important;
        height: 235px;
        width: 195px;
        // cursor: pointer;
        display: flex;
        flex-direction: column;
        transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
            background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms !important;
        align-items: center;
        .auth-card-img {
            height: 100px;
            width: 100px;
        }
        .auth-card-item-select-box {
            position: absolute;
            top: 4px;
            right: 4px;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 2rem;
            height: 2rem;
            svg {
                width: 90%;
                height: 90%;
                color: #fff;
            }
        }
        .auth-card-top {
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
        .auth-card-title {
            box-sizing: border-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 3;
            text-overflow: ellipsis;
            color: #fff;
            font-weight: 700;
            font-size: 1.2rem;
            word-wrap: break-word;
            overflow: hidden;
            text-align: center;
            margin-top: 8px;
        }
        .card-item-footer-bar {
            display: flex;
            box-sizing: border-box;
            flex-direction: column;
            justify-content: center;
            background: #5048a8;
            padding: 10px 5px;
            width: 100%;
            .card-item-footer-bar-box {
                display: flex;
                align-items: center;
                justify-content: center;
                color: #fff;
                .card-item-footer-bar-box-text {
                    font-size: 0.8rem;
                }
            }
        }
    }
}
.auth-card-button-mltiple {
    box-shadow: 8px 8px #7367f04d;
}
