@import "../../../themes/themes-vars.module";
.business-hour {
  .title_text {
    font-size: 20px;
    font-weight: bold;
    margin-right: 15px;
  }
  .simple {
    ._top {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
    .menu_text {
      font-size: 16px;
      font-weight: bold;
    }
    .m_10 {
      margin-top: 10px;
    }
    ._menu {
      flex-wrap: wrap;
      .btn {
        margin: 5px 10px;
        border-radius: 6px;
        padding: 6px 15px;
      }
      .active_bac {
        background-color: $primaryMain;
      }
      .bac {
        // background-color: #f5f5f5;
      }
      .active_text {
        color: #fff;
      }
      // .disable_text {
      //   background-color: #0000001f;
      //   color: #00000042;
      // }
    }
    .table-top {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      .menu_text {
        font-size: 16px;
        font-weight: bold;
        margin-right: 10px;
      }
      .m_10 {
        margin-top: 10px;
      }
    }
    .table {
      margin-top: 20px;
      .menuGroup {
        display: flex;
        flex-direction: row;
        .text {
          margin-right: 10px;
        }
      }
      .add_new {
        border: 1px solid #7367f0;
        border-radius: 20px;
        padding: 20px;
        .search-picker {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          .picker {
            display: flex;
            flex-direction: row;
            align-items: center;
            .ml_10 {
              margin-left: 10px;
              margin-right: 10px;
            }
          }
        }
      }
    }
  }
}
// 复合型营业时间
.business-hour-compound {
  margin-left: 24px;
  .business-hour-compound-title {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .business-hour-compound-box {
    flex: 1;
    max-width: 1200px;
  }
  .business-hour-compound-card {
    display: flex;
    margin-top: 16px;
    .business-hour-top {
      display: flex;
      align-items: center;
      margin-bottom: 16px;
    }
  }
  .form-error {
    color: red;
    margin-bottom: 6px;
    margin-top: -10px;
    display: none;
    &.active {
      display: block;
    }
  }
}
