@import "../../themes/themes-vars.module";

$expandWidth: 260px;
$reducedWidth: 84px;

.app-shell {
    height: 100%;
    display: flex;
    .sidebar-unique {
        flex: 0 0 auto;
        overflow-x: hidden;
        width: $expandWidth;
        z-index: 111;
        transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        color: #6e6e6e;
        .nav-bar {
            color: rgba(51, 48, 60, 0.87);
            transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
            background-image: none;
            // overflow-x: hidden;
            // overflow-y: scroll;
            display: flex;
            flex-direction: column;
            height: 100%;
            flex: 1 0 auto;
            z-index: 1200;
            position: fixed;
            top: 0px;
            outline: 0px;
            left: 0px;
            background-color: rgb(255, 255, 255);
            width: inherit;
            border-right: 0px solid;
        }
        .nav-bar .nav-header {
            display: flex;
            -webkit-box-align: center;
            align-items: center;
            -webkit-box-pack: justify;
            justify-content: space-between;
            padding-right: 1.125rem;
            transition: padding 0.25s ease-in-out 0s;
            min-height: 64px;
            padding-left: 1.125rem;
            z-index: 1;
            background: inherit;
            .logo {
                width: 48px;
            }
            .project-name {
                // color: #33303cde;
                flex: 1 1 auto;
                font-weight: 600;
                line-height: 24px;
                font-size: 1.375rem !important;
                font-family: Public Sans, sans-serif, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
                    "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
                transition: $primaryTransitionOpacity;
            }
        }
        .title .MuiTypography-body1 {
            opacity: 1;
            white-space: nowrap;
            overflow: hidden;
            transition: $primaryTransitionOpacity;
            display: flex;
        }
        .MuiListItemButton-root {
            padding: 0.5rem 1rem;
            margin: 0.25rem 0.875rem 0;
        }
        .expandIcon {
            font-size: 16px;
            opacity: 1;
            white-space: nowrap;
            overflow: hidden;
        }
        .MuiListItemText-root {
            margin-top: 0;
            margin-bottom: 0;
        }
        .active {
            color: #fff;
            box-shadow: rgb(115 103 240 / 48%) 0px 2px 6px;
            background: linear-gradient(72.47deg, rgb(115, 103, 240) 22.16%, rgba(115, 103, 240, 0.7) 76.47%);
            border-radius: 6px;
            .MuiSvgIcon-root {
                color: #fff;
            }
        }
        .active-title-text .MuiTypography-body1 {
            // font-weight: bold;
        }
        .default-icon {
            // color: #33303c99;
            min-width: 40px;
        }
        .active-parents-icon {
            color: $primaryMain;
        }
        .MuiListItemButton-root:hover {
            border-radius: 6px;
        }
        .MuiListItemButton-root:hover .MuiSvgIcon-root {
            // color: $primaryMain;
        }

        .foldBtn {
            position: fixed;
            bottom: 20px;
            left: 0;
            width: 30px;
            height: 50px;
            // background-color: $primaryMain;
            // box-shadow: 5px 0 10px -2px #ccc;
            border-top-right-radius: 6px;
            border-bottom-right-radius: 6px;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            opacity: 0;
            transition: $primaryTransitionOpacity;
            z-index: 1201;
            // &:hover {
            //     background-color: $primaryMain;
            // }
        }
        &:hover .foldBtn {
            opacity: 1;
        }
        .nav-item-children {
            .title {
                margin-left: 0.5rem;
            }
        }
        .active-nav-item-parents {
            // box-shadow: rgb(115 103 240 / 48%) 0px 2px 6px;
            background: rgba(47, 43, 61, 0.06);
            border-radius: 6px;
            // .MuiSvgIcon-root {
            //     color: #fff;
            // }
            .default-icon {
                color: $primaryMain !important;
            }
        }
    }
    .sidebar-closed {
        .active-nav-item-parents{
            background: unset;
        }
        .nav-bar {
            // overflow-x: unset;
        }
        // width: $reducedWidth;
        transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        .title .MuiTypography-body1 {
            // width: 0;
            opacity: 0;
            // display: none;
            overflow: hidden;
            transition: $primaryTransitionOpacity;
        }
        .expandIcon {
            width: 0;
            opacity: 0;
            overflow: hidden;
        }
        .collapse-box {
            position: absolute;
            left: 70px;
            z-index: 1;
            opacity: 0;
            width: 190px;
            border-bottom-right-radius: 8px;
            display: none;
            box-shadow: rgb(115 103 240 / 48%) 0px 2px 6px;
            border-radius: 6px;
            padding-left: 8px;
            // height: 60vh !important;
            overflow-y: scroll;
            overflow-x: hidden;
            .MuiListItemButton-root {
                padding: 12px;
                border-bottom-right-radius: 6px;
                padding-left: 5px;
                border-width: 0;
            }
            .MuiListItemIcon-root {
                display: none;
            }
        }
        .MuiListItemButton-root:hover {
            // color: $primaryMain;
            background-color: transparent;
            backdrop-filter: blur(12px);
        }
        .active {
            background-color: transparent;
        }
        .nav-item {
            position: relative;
            width: 56px;
            transition: width 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        }
        .nav-item:hover,
        .tabletTabIds {
            width: 260px;
            border-radius: 6px;
            // border-top-right-radius: 6px;
            // border-top-left-radius: 6px;
            margin: 0.25rem 0.875rem 0;
            box-shadow: rgb(115 103 240 / 48%) 0px 2px 6px;

            .nav-item-parents {
                margin: 0;
                border-top-right-radius: 8px;
                backdrop-filter: blur(6px);
                cursor: default;
                .title .MuiTypography-body1 {
                    font-weight: bold;
                    overflow: hidden;
                }
            }

            .noShadow {
                box-shadow: unset;
                width: 100%;
                margin: 0;
                .nav-item-children {
                    width: auto !important;
                }
            }
            // .MuiListItemButton-root {
            .title .MuiTypography-body1 {
                width: 150px;
                opacity: 1;
                display: block;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
            .expandIcon {
                width: auto;
                opacity: 1;
                overflow: hidden;
            }
            // }
            .collapse-box {
                opacity: 1;
                display: block;
                backdrop-filter: blur(6px);
                border-bottom-right-radius: 6px;
            }
            .collapse-box-non-back {
                opacity: 1;
                display: block;
                border-bottom-right-radius: 6px;
                .MuiListItemButton-root {
                    backdrop-filter: blur(6px);
                }
            }
        }
    }
    .sidebar-closed-mobile {
        box-shadow: 1px 0 5rem 0px #4b4b5a57;
        background-color: #fff;
        overflow: hidden;
        position: absolute;
        .nav-bar {
            overflow: hidden;
        }
    }
    .content-page {
        -webkit-box-flex: 1;
        flex-grow: 1;
        min-width: 0px;
        display: flex;
        min-height: 100vh;
        flex-direction: column;
        overflow: auto;
        .content-page-container {
            margin-top: calc(64px + 1rem);
            -webkit-box-flex: 1;
            flex-grow: 1;
            width: 100%;
            padding: 1.5rem;
            transition: padding 0.25s ease-in-out 0s;
            margin-left: auto;
            margin-right: auto;
        }
    }
    .mask {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: #00000033;
        z-index: 103;
        backdrop-filter: blur(1px);
        transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    }
}

.shop-package-area {
    padding: 10px;
    .grid-delete {
        display: flex;
        justify-content: flex-start;
        padding: 8px 0;
    }
    .btn {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        background-color: $primaryMain;
        color: #fff;
        font-size: 15px;
        margin-bottom: 10px;
    }
    .btn:hover {
        background-color: $primaryMain;
    }
    .list-card {
        box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14),
            0px 1px 5px 0px rgba(0, 0, 0, 0.12);
    }
    .shop-package-area-add {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        cursor: pointer;
        width: 100%;
    }
}